import React from "react";
import styled from "styled-components";
import Foundation from "./Foundation";
const CenterWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
  flex-direction: column;
`;

const SectionContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 940.5px;
  color: #fff;
  margin-bottom: 50px;
`;

const LeftHalf = styled.div`
  flex: 0.3;
`;

const RightHalf = styled.div`
  flex: 0.3;
`;

const Paragraph = styled.p`
  font-family: Archivo;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 1px;
  text-align: left;
  width: 331px;
  color: #d0d0d0;
`;
const Para = styled.p`
  font-family: Archivo;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 1px;
  text-align: left;
  color: #d0d0d0;
  margin-top: 0px;
  width:200px;
`;

const Heading = styled.span`
  font-family: Archivo;
  font-size: 50px;
  font-weight: 500;
  line-height: 58px;
  letter-spacing: -1px;
  text-align: left;
`;

const Heading1 = styled.span`
  font-family: Archivo;
  font-size: 50px;
  font-style: italic;
  font-weight: 100;
  line-height: 58px;
  letter-spacing: -1px;
  text-align: left;
  color: #82e8f2;
`;
const Text = styled.p`
  font-family: Archivo;
  font-size: 12px;
  font-weight: 300;
  line-height: 22px;
  letter-spacing: 8px;
  text-align: left;

  color: #fff;
`;

const Inner = {
  width: "240px",
  borderRadius: "20px",
  background: "#292D32",
  padding: "32px",
  display: "flex",
  flexDirection: "column",
  justifyContent: "flex-start",
  marginTop:'20px'
};

function OurValues() {
  return (
    <>
      <CenterWrapper>
        <SectionContainer>
          <LeftHalf>
            <Heading>Our </Heading>
            <Heading1>Values</Heading1>
          </LeftHalf>
          <RightHalf>
            <Text>WHAT WE BELIEVE</Text>
            <Paragraph>
              The client is paramount, and we prioritize dedicated support for
              our clients. Welcome to the forefront of exceptional service at
              Reset Digital.
            </Paragraph>
          </RightHalf>
        </SectionContainer>

        <Foundation />

        <div
          style={{ display: "flex", flexDirection: "row", marginTop: "50px" }}
        >
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              marginRight: "20px",
            }}
          >
            <div style={Inner}>
              <img
                src="https://i.ibb.co/fCbrWBv/image.png"
                height="64px"
                width="64px"
              />
              <Text>HARDWORK</Text>

              <Para>
                The client is paramount, and we prioritize dedicatedsupport for
                our clients. Welcome to the forefront of exceptional service at
                Reset Digital.
              </Para>
            </div>
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              marginRight: "20px",
            }}
          >
            <div style={Inner}>
              <img
                src="https://i.ibb.co/fCbrWBv/image.png"
                height="64px"
                width="64px"
              />
              <Text>INNOVATION</Text>

              <Para>I am very sorry for the pain, it will be followed.</Para>
            </div>
            <div style={Inner}>
              <img
                src="https://i.ibb.co/fCbrWBv/image.png"
                height="64px"
                width="64px"
              />
              <Text>TEAMWORK</Text>

              <Para>I am very sorry for the
pain, it will be
followed.</Para>
            </div>
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              marginRight: "20px",
            }}
          >
            <div style={Inner}>
              <img
                src="https://i.ibb.co/fCbrWBv/image.png"
                height="64px"
                width="64px"
              />
              <Text>GROWTH</Text>

              <Para>
              I am very sorry for the
pain, it will be
followed.
              </Para>
            </div>
          </div>
        </div>
      </CenterWrapper>
    </>
  );
}

export default OurValues;
