import React, { useRef, useEffect } from "react";
import styled from "styled-components";

const CenterWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  flex-direction: column;
`;

const SectionContainer = styled.section`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #fff;
`;

const LeftHalf = styled.div`
  flex: 0.7;
  width: 100%;
`;

const RightHalf = styled.div`
  flex: 0.29;
`;

const Paragraph = styled.p`
  font-family: Archivo;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 1px;
  text-align: center;
  width: 360px;
`;

const Heading = styled.span`
  font-family: Archivo;
  font-size: 50px;
  font-weight: 500;
  line-height: 58px;
  letter-spacing: -1px;
  text-align: left;
`;

const Heading1 = styled.span`
  font-family: Archivo;
  font-size: 50px;
  font-style: italic;
  font-weight: 100;
  line-height: 58px;
  letter-spacing: -1px;
  text-align: left;
  color: #82e8f2;
`;
const Text = styled.p`
  font-family: Archivo;
  font-size: 12px;
  font-weight: 300;
  line-height: 22px;
  letter-spacing: 8px;
  text-align: left;

  margin-top: 50px;
  color: #fff;
`;
const Section = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`;

const HeadingTop = styled.span`
  font-family: Archivo;
  font-size: 32px;
  font-weight: 500;
  line-height: 42px;
  letter-spacing: -1px;
  text-align: center;
  color: #fff;
  width: 260px;
`;

const Price = styled.span`
  //styleName: Font 5/Archivo/Light 20.8 title;
  font-family: Archivo;
  font-size: 21px;
  font-weight: 300;
  line-height: 24px;
  letter-spacing: -1px;
  text-align: left;
  color: #fff;
  margin: 30px;
`;

const PText = styled.p`
  font-family: Archivo;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 1px;
  text-align: center;
  color: #d0d0d0;
  width: 240px;
`;
const PIncludes = styled.p`
  font-family: Archivo;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 1px;
  text-align: center;
  padding: 12px;
  color: #d0d0d0;
  border-bottom: 1px solid #9999994d;
  width: 240px;
  margin-top:0px;
`;

const Button = styled.button`
width: 159.02px;
height: 50px;
background: #82E8F2;
border-radius: 100px;
font-family: Archivo;
font-size: 14px;
font-weight: 300;
line-height: 26px;
letter-spacing: 1px;
text-align: center;
margin-top: 20px;

`

function OurPackages() {
  return (
    <>
      <CenterWrapper>
        <SectionContainer>
          <Text>options</Text>
          <div>
            <Heading>Our </Heading>
            <Heading1>Packages</Heading1>
          </div>

          <Paragraph>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vestibulum
            convallis.
          </Paragraph>
        </SectionContainer>

        <Section>
          <div style={{ overflow: "hidden", position: "relative" }}>
            <div
              style={{
                width: "100%",
                display: "flex",
                flexWrap: "wrap",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              {data.map((item, index) => (
                <React.Fragment key={item.id}>
                  <Container item={item} />
                  {index % 2 !== 0 && <div style={{ width: "100%" }} />}
                </React.Fragment>
              ))}
            </div>
          </div>
        </Section>
      </CenterWrapper>
    </>
  );
}
const Container = ({ item }) => {
  return (
    <div
      style={{
        margin: "15px",
        padding: "60px",
        backgroundColor: "#262626",
        borderRadius: "10px",
        width: "360px",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        flexDirection: "column",
      }}
    >
      <HeadingTop>{item.PackageName}</HeadingTop>

      <Price>{item.Price}</Price>

      <PText>{item.Description}</PText>

      <PIncludes>{item.include1}</PIncludes>
      <PIncludes>{item.include2}</PIncludes>
      <PIncludes>{item.include3}</PIncludes>

      <Button>
        Get Started
      </Button>
    </div>
  );
};
const data = [
  {
    id: 1,
    PackageName: "Basic Branding Package",
    Price: "$ 599.00 CAD",
    Description:
      "Ideal for startups and small businesses seeking to establish a strong brand identity.",
    include1: "Custom Logo Design",
    include2: "Brand Color Palette",
    include3: "Basic Brand Style Guide",
  },
  {
    id: 2,
    PackageName: "Professional Web Design Package",
    Price: "$$ 1,499.00 CAD",
    Description:
      "Perfect for businesses looking for a user-friendly and visually appealing website.",
    include1: "Custom Website Design",
    include2: "SEO Optimization",
    include3: "10 Pages",
  },
  {
    id: 3,
    PackageName: "Premium Design & Marketing Package",
    Price: "$ 2,399.00 CAD",
    Description:
      "An all-in-one package for businesses looking for a complete design and marketing solution.",
    include1: "Website Design & Development",
    include2: "Social Media Graphics",
    include3: "SEO Services",
  },
];

export default OurPackages;
