import React from "react";
import styled from "styled-components";

const SectionContainer = styled.section`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 150px;
`;
const Text = styled.div`
  font-family: Archivo;
  font-size: 12px;
  font-weight: 300;
  line-height: 22px;
  letter-spacing: 8px;
  text-align: center;

  color: #fff;
`;
const Heading1 = styled.span`
  font-family: Archivo;
  font-size: 50px;
  font-weight: 500;
  line-height: 58px;
  letter-spacing: -1px;

  color: #fff;
`;
const Heading2 = styled.span`
  font-family: Archivo;
  font-size: 50px;
  font-style: italic;
  font-weight: 100;
  line-height: 58px;
  letter-spacing: -1px;

  color: #82e8f2;
`;

const SectionHeading = styled.div``;

const Para = styled.p`
font-family: Archivo;
font-size: 16px;
font-weight: 300;
line-height: 26px;
letter-spacing: 1px;
text-align: center;
color:#D0D0D0;
`;

const Role = styled.p`
color:#82E8F2;
font-family: Archivo;
font-size: 12px;
font-weight: 300;
line-height: 11px;
letter-spacing: 8px;
text-align: left;


`
const OuterDiv ={
    height:'355px' ,width:'309px',flexDirection:'column', display:'flex',marginRight:"70px",justifyContent:'center',alignItems:'center'
}
const InnerDiv={width:"200px",height:"70px",backgroundColor:"#292D32",marginTop:'-35px',display:'flex',alignItems:'flex-end',justifyContent:'center'}

const Team = () => {
  return (
    <div>
      <SectionContainer>
        <Text>WHO WE ARE</Text>
        <SectionHeading>
          <Heading1>Meet Our </Heading1>
          <Heading2>Team </Heading2>
        </SectionHeading>
        <Para>Our group of experts set to bring your ideas to reality.  </Para>
        <div style={{flexDirection:'row', display:'flex', justifyContent:'space-between',marginTop:'100px'}}>

        <div style={OuterDiv}>
            <img src='https://i.ibb.co/bFfVMtS/image.png' height='320px' width="309px"/>
            <div style={InnerDiv}>
                <Role>CEO</Role>

            </div>
        </div>
        <div style={OuterDiv}>
            <img src='https://i.ibb.co/bFfVMtS/image.png' height='320px' width="309px"/>
            <div style={InnerDiv}>
                <Role>MANAGER</Role>

            </div>
        </div>
        <div style={OuterDiv}>
            <img src='https://i.ibb.co/bFfVMtS/image.png' height='320px' width="309px"/>
            <div style={InnerDiv}>
                <Role>ENGINEER</Role>

            </div>
        </div>
        </div>

   
      </SectionContainer>
    </div>
  );
};

export default Team;
