import React from "react";
import styled from "styled-components";

const CenterWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
`;

const SectionContainer = styled.section`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #fff;
`;

const Paragraph = styled.p`
  font-family: Archivo;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 1px;
  text-align: center;
  width: 360px;
  color: #d0d0d0;
  margin-bottom: 0;
`;

const Heading = styled.span`
  font-family: Archivo;
  font-size: 50px;
  font-weight: 500;
  line-height: 58px;
  letter-spacing: -1px;
  text-align: left;
`;

const Heading1 = styled.span`
  font-family: Archivo;
  font-size: 50px;
  font-style: italic;
  font-weight: 100;
  line-height: 58px;
  letter-spacing: -1px;
  text-align: left;
  color: #82e8f2;
`;
const Text = styled.p`
  font-family: Archivo;
  font-size: 12px;
  font-weight: 300;
  line-height: 22px;
  letter-spacing: 8px;
  text-align: left;

  margin-top: 50px;
  color: #fff;
`;

const Button = styled.button`
  width: 159.02px;
  height: 50px;
  background: #82e8f2;
  border-radius: 100px;
  font-family: Archivo;
  font-size: 14px;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 1px;
  text-align: center;
  margin-top: 20px;
`;
const Outter = {
  backgroundColor: "#292D32",
  width: "996px",
  padding: "40px",
};

const Form = {
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  gap: "30px",
};

const Input = {
  borderRadius: "5px",
  padding: "16px",
};
const InputMessage = {
  borderRadius: "5px",
  padding: "16px",
};
function ContactUs() {
  return (
    <>
      <CenterWrapper>
        <div style={Outter}>
          <SectionContainer>
            <Text>GET IN TOUCH</Text>
            <div>
              <Heading> Request a Free</Heading>

              <Heading1> Consultation </Heading1>
            </div>

            <Paragraph>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit.
              Vestibulum convallis.
            </Paragraph>
          </SectionContainer>

          <div style={Form}>
            <div style={{ display: "flex", flexDirection: "column" }}>
              <Paragraph style={{ textAlign: "left" }}>Name</Paragraph>
              <input type="text" placeholder="Your Name" style={Input} />
            </div>
            <div style={{ display: "flex", flexDirection: "column" }}>
              <Paragraph style={{ textAlign: "left" }}>Email</Paragraph>
              <input type="text" placeholder="Your Email" style={Input} />
            </div>
          </div>
          <div style={Form}>
            <div style={{ display: "flex", flexDirection: "column" }}>
              <Paragraph style={{ textAlign: "left" }}>Phone Number</Paragraph>
              <input type="text" placeholder="+ 1009 12345" style={Input} />
            </div>
            <div style={{ display: "flex", flexDirection: "column" }}>
              <Paragraph style={{ textAlign: "left" }}>Service</Paragraph>
              <input type="text" placeholder="Consultation" style={Input} />
            </div>
          </div>
          <div style={Form}>
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                width: "750px",
              }}
            >
              <Paragraph style={{ textAlign: "left" }}>Message</Paragraph>
              <input
                type="text"
                placeholder="Your Message"
                style={InputMessage}
              />
              <Button>Send Request</Button>
            </div>
          </div>
        </div>
      </CenterWrapper>
    </>
  );
}

export default ContactUs;
